"use client";

import * as React from "react";
import { getAvailableYearsAction } from "@/app/actions";

interface YearSelectorProps {
  selectedYear: number;
  onYearChange: (year: number) => void;
}

export function YearSelector({ selectedYear, onYearChange }: YearSelectorProps) {
  const [availableYears, setAvailableYears] = React.useState<number[]>([selectedYear]);
  const [isLoading, setIsLoading] = React.useState(true);

  React.useEffect(() => {
    async function loadYears() {
      try {
        const years = await getAvailableYearsAction();
        setAvailableYears(years);
      } catch (error) {
        console.error("Error loading years:", error);
      } finally {
        setIsLoading(false);
      }
    }
    loadYears();
  }, []);

  if (isLoading) {
    return (
      <select
        disabled
        className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
      >
        <option>Loading...</option>
      </select>
    );
  }

  return (
    <select
      value={selectedYear}
      onChange={(e) => onYearChange(Number(e.target.value))}
      className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
    >
      {availableYears.map((year) => (
        <option key={year} value={year}>
          {year}
        </option>
      ))}
    </select>
  );
}

